<template>
    <div id="app" class="mod-speak">
        <div class="txt">{{ eyan }}</div>
        <div class="by"> {{ from }}{{ who }}</div>
        <ul class="btn-list">
            <li>
                <a
                    class="btn-normal btn-heart"
                    href="javascript:;"
                    @click="likeit()"
                    ><svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-xin"></use></svg
                    ><span class="n" v-if="num !== 0">{{ num }}</span></a
                >
                <span class="pop" v-if="poplike"
                    ><svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-xin"></use></svg
                ></span>
            </li>
            <li>
                <a class="btn-normal" href="javascript:;" @click="querydata()"
                    ><svg
                        class="icon"
                        :class="{ rotate: loading }"
                        aria-hidden="true"
                    >
                        <use xlink:href="#icon-genghuan"></use></svg
                ></a>
            </li>
            <li>
                <a class="btn-normal" href="javascript:;" @click="copytxt()"
                    ><svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-fuzhi"></use></svg
                ></a>
                <span class="hint" v-if="copyed"></span>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: "app",
    data() {
        return {
            loading: true,
            poplike: false,
            copyed: false,
            eyan: "",
            from: "",
            who: "",
            num: 0,
        };
    },
    mounted: function () {
        this.querydata();
    },
    methods: {
        querydata() {
            let that = this;

            that.loading = true;
            that.eyan = "";
            that.who = "";
            that.num = 0; //like

            fetch("//v1.hitokoto.cn/")
                .then(function (response) {
                    return response.json();
                })
                .then(function (json) {
                    that.eyan = json.hitokoto;
                    that.from = "" + json.from + "";
                    // that.who = json.creator;
                    that.loading = false;

                    console.log("parsed json", json);
                })
                .catch(function (ex) {
                    console.log("Err:", ex);
                });
        },
        likeit() {
            if (this.poplike) return;

            let that = this,
                num = that.num;

            num++;

            that.poplike = true;
            that.num = num;

            setTimeout(function () {
                that.poplike = false;
            }, 500);
        },
        copytxt() {
            let that = this;

            if (this.copyed) return;

            //  input 
            var aux = document.createElement("input");
            // 
            aux.setAttribute("value", that.eyan);
            //  DOM 
            document.body.appendChild(aux);
            // 
            // :  input  textarea  select() .
            aux.select();
            // 
            var content = window.getSelection().toString();
            // 
            document.execCommand("copy");
            //  input 
            document.body.removeChild(aux);

            // hui.toast('');
            that.copyed = true;
            console.log("" + that.eyan);
            setTimeout(function () {
                that.copyed = false;
            }, 2200);
        },
    },
};
</script>

<style scoped>
</style>


